.custom-product-block-fe ul.products {
    display: grid;
    grid-template-columns: repeat(3, 1fr );
    gap: 20px;
    margin: 0;
}

.custom-product-block-fe ul.products li {
    border: 1px solid #ddd;
    border-radius: 3px;
    list-style: none;
}

.custom-product-block-fe ul.products li .product-content{
    padding: 20px;
}
.custom-product-block-fe ul.products.grid-1 {
    grid-template-columns: repeat(1, 1fr );
}
.custom-product-block-fe ul.products.grid-2 {
    grid-template-columns: repeat(2, 1fr );
}

.custom-product-block-fe ul.products.grid-4 {
    grid-template-columns: repeat(4, 1fr );
}

.custom-product-block-fe ul.products.grid-5 {
    grid-template-columns: repeat(5, 1fr );
}

.custom-product-block-fe ul.products li.product {
    width: unset !important;
    float: left !important;
    margin: unset !important;
    clear: none !important;
}

.custom-product-block-fe ul.products::before, 
.custom-product-block-fe ul.products::after {
    display: inline-block;
    content: unset;
}